<template>

<nav class="clearfix">
    <div class="nav0">
        <div class="nav1">
            <!-- https://bomango.tistory.com/52 -->
            <ul class="nav1-main" v-on:mouseleave="mouseleave" >
                <li>
                    <router-link to="/category" v-on:mouseover="mouseover"></router-link>
                    <ul class="nav1-sub" v-show="show">
                        <li v-on:click.prevent="showProductList('bed')"><a></a></li>
                        <li v-on:click.prevent="showProductList('mattress')"><a></a></li>
                        <li v-on:click.prevent="showProductList('frame')"><a></a></li>
                    </ul>
                </li>
                <li>
                    <router-link to="/category"  v-on:mouseover="mouseover"></router-link>
                    <ul class="nav1-sub" v-show="show">
                        <li v-on:click.prevent="showProductList('normal-sofa')"><a></a></li>
                        <li v-on:click.prevent="showProductList('lounge-sofa')"><a></a></li>
                        <li v-on:click.prevent="showProductList('single-sofa')"><a>1 </a></li>
                    </ul>
                </li>
                <li>
                    <router-link to="/category"  v-on:mouseover="mouseover" ></router-link>
                    <ul class="nav1-sub" v-show="show">
                        <li v-on:click.prevent="showProductList('dining-table')"><a></a></li>
                        <li v-on:click.prevent="showProductList('side-table')"><a> </a></li>
                    </ul>
                </li>
                <li>
                    <router-link to="/category" v-on:mouseover="mouseover"></router-link>
                    <ul class="nav1-sub" v-show="show">
                        <li v-on:click.prevent="showProductList('closet')"><a></a></li>
                        <li v-on:click.prevent="showProductList('hanger')"><a></a></li>
                        <li v-on:click.prevent="showProductList('built-in')"><a></a></li>
                    </ul>
                </li>
            </ul>
        </div>
        <!-- nav1 -->
        
        <!-- https://apost.dev/1121/#google_vignette -->
        <div class="rollingbanner">
            <div class="title" >&nbsp;</div>
            <ul class="nav2-hit">
                <li  class="current"><router-link to="/">1.</router-link></li>
                <li class="next"><router-link to="/">2.</router-link></li>
                <li><router-link to="/">3.</router-link></li>
                <li><router-link to="/">4.1</router-link></li>
                <li><router-link to="/">5.</router-link></li>
                <li><router-link to="/">6.</router-link></li>
                <li><router-link to="/">7.</router-link></li>
                <li><router-link to="/">8.</router-link></li>
                <li><router-link to="/">9.</router-link></li>
                <li class="prev"><router-link to="/">10.</router-link></li>
            </ul>
        </div>

        
    </div>
    <!-- nav0 -->
</nav>
<!-- //nav  -->

</template>

<script>
import '@/assets/css/main/ss-home.css';
import '@/assets/css/main/ss-main.css';
import axios from 'axios';

export default {
    name : 'AppNavView',
    components : {},
    data (){
        return{
            show : false, //  
            category : '',
            hit1 : '',
            hit2 : '',
            hit3 : '',
            hit4 : '',
            hit5 : '',
            hit6 : '',
            hit7 : '',
            hit8 : '',
            hit9 : '',
            hit10 : ''
        }
    },
    methods : {
        mouseover(){ //    
            this.show = true;
        },
        mouseleave(){ //    
            this.show = false;
        },
        showProductList(category){ 
            this.$store.commit('setCategory',category);
            if(this.$route.path != '/category'){
                this.$router.push('/category');
            } else {
                this.$emit('list',category);
            }   
        },
        getHitList(){
            if(this.$store.state.hitList == ""){
                axios({
                    method: 'get', // put, post, delete 
                    url: `${this.$store.state.apiBaseUrl}/home/main/hits`,
                    headers: { "Content-Type": "application/json; charset=utf-8" }, //
                    // params: , //get   
                    // data: , //put, post, delete   JSON  
                    responseType: 'json' //
                }).then(response => {
                    let list = response.data.apiData;
                    this.$store.commit('setHitList',response.data.apiData);
                    this.hit1 = list[0];
                    this.hit2 = list[1];
                    this.hit3 = list[2];
                    this.hit4 = list[3];
                    this.hit5 = list[4];
                    this.hit6 = list[5];
                    this.hit7 = list[6];
                    this.hit8 = list[7];
                    this.hit9 = list[8];
                    this.hit10 = list[9];

                }).catch(error => {
                    console.log(error);
                });
            } else {
                // console.log(this.hitList);
            }
        },
        rollingCallback(){
            //.prev  
            document.querySelector('.rollingbanner .prev').classList.remove('prev');

            //.current -> .prev
            let current = document.querySelector('.rollingbanner .current');
            current.classList.remove('current');
            current.classList.add('prev');

            //.next -> .current
            let next = document.querySelector('.rollingbanner .next');
            //    
            if(next.nextElementSibling == null){
                document.querySelector('.rollingbanner ul li:first-child').classList.add('next');
            }else{
                //     
                next.nextElementSibling.classList.add('next');
            }
            next.classList.remove('next');
            next.classList.add('current');
        }
    },
    created(){
        this.getHitList();
        document.addEventListener('DOMContentLoaded', ()=>{
            let interval = window.setInterval(this.rollingCallback, 3000);
            document.querySelector('.rollingbanner').addEventListener('mouseover',function(){
                window.clearInterval(interval);
            })
            document.querySelector('.rollingbanner').addEventListener('mouseout',function(){
                interval = window.setInterval(this.rollingCallback, 3000);
            })
        })
    }
}
</script>

